@import url("https://fonts.googleapis.com/css2?family=Playpen+Sans&display=swap");

.App {
  min-height: 100vh;
  display: flex;
  font-family: Playpen Sans;
  background: #f4f4f4;
}

::-webkit-scrollbar {
  width: 0px;
}

::-webkit-scrollbar-thumb {
  background: rgba(136, 136, 136, 0.281);
}

/* Handle on hover */
::-webkit-scrollbar-thumb:hover {
  background: #555;
}
